body {
  font-family: "Roboto", Arial, sans-serif;
  font-weight: medium;
  color: #fff; }

.button {
  background: none;
  border: none;
  cursor: pointer; }

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 100%;
  min-height: 100vh;
  background-color: #3767AD;
  padding: 1rem; }
  .app__header {
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    z-index: 1;
    padding: 1rem; }
  .app__container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60%;
    height: 100%;
    max-width: 1200px;
    margin-top: 4rem;
    flex-direction: column; }
  .app__button {
    border-radius: 4px;
    padding: 12px;
    min-width: 200px;
    color: #fff;
    font-size: 0.875rem;
    transition: box-shadow 0.2s, background-color 0.2s; }
    .app__button--add {
      font-size: 1.125rem;
      background-color: #fff;
      color: #000;
      margin-bottom: 2rem;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }
      .app__button--add:hover {
        box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.5); }
    .app__button--remove {
      margin-bottom: 1rem; }
      .app__button--remove:hover {
        background-color: #2196f3; }

.navigation__list {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: flex-end;
  list-style-type: none;
  margin: 0;
  padding: 0; }

.navigation__button {
  color: #fff;
  padding: 0.5rem;
  border: 2px solid transparent; }
  .navigation__button:hover {
    border-bottom-color: #fff; }

.logo {
  flex-grow: 1; }
  .logo__link {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    max-width: 120px; }
  .logo__svg {
    fill: #2196f3;
    width: 100%;
    height: 100%; }
    .logo__svg--color {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      fill: #fff; }

.glass {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative; }
  .glass__svg {
    width: 100%;
    max-height: 400px;
    margin-bottom: 48px; }
  .glass__fill {
    fill: #2196f3; }
  .glass__outline {
    fill: none;
    stroke: #fff;
    stroke-width: 10px; }
  .glass__counter {
    margin: 0;
    text-align: center;
    font-size: 7rem;
    position: absolute; }

.archive {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #3767AD;
  font-weight: 300;
  display: none;
  visibility: hidden;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 4rem; }
  .archive--visible {
    display: flex;
    visibility: visible; }
  .archive--on-top {
    z-index: 2; }
  .archive__overlay {
    background-color: rgba(0, 0, 0, 0.3);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    visibility: hidden;
    z-index: 1;
    opacity: 0;
    transition: opacity 0.2s, visibility 0s 0.2s; }
    .archive__overlay--visible {
      opacity: 1;
      visibility: visible;
      transition: opacity 0.2s, visibility 0.2s; }
  .archive__list {
    list-style: none;
    padding: 0 1rem;
    margin: 0;
    margin-top: 1rem;
    width: 100%;
    height: 100%;
    overflow: auto;
    scroll-behavior: smooth; }
  .archive__item {
    display: none;
    grid-template-columns: 1fr repeat(3, auto);
    grid-column-gap: 5px;
    align-items: center;
    padding: 0.4rem;
    border-top: 1px solid transparent;
    border-bottom: 1px solid transparent;
    transition: box-shadow 0.2s; }
    .archive__item--visible {
      display: grid; }
    .archive__item--empty {
      text-align: center;
      font-style: italic;
      display: flex;
      justify-content: center;
      align-items: center; }
    .archive__item:nth-child(even) {
      background-color: rgba(255, 255, 255, 0.05); }
    .archive__item:not(:last-child) {
      border-bottom: 1px solid #ccc; }
  .archive .archive__item--on-top {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.3);
    background-color: #3767AD;
    position: relative;
    border-color: #fff;
    z-index: 1; }
  .archive__date {
    margin: 0; }
  .archive__value {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    font-weight: bold;
    width: 25px;
    height: 25px;
    border: 2px solid #fff;
    border-radius: 50%; }
    @media only screen and (min-width: 360px) {
      .archive__value {
        width: 30px;
        height: 30px; } }
  .archive__button {
    padding: 0.6rem 1rem;
    border-radius: 4px;
    color: #fff;
    font-size: 1rem;
    transition: background-color 0.2s;
    margin: 1rem 0;
    visibility: visible; }
    .archive__button--hidden {
      visibility: hidden; }
    .archive__button:hover {
      background-color: #2196f3; }

.edition {
  position: relative;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  column-gap: 5px;
  overflow: hidden;
  width: 25px;
  transition: width 0.5s; }
  .edition--visible {
    width: 115px; }
  .edition__button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    width: 25px;
    height: 25px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s, visibility 0s 0.5s; }
    .edition__button--visible {
      visibility: visible;
      opacity: 1;
      transition: opacity 0.5s, visibility 0s; }
    .edition__button--edit {
      position: absolute; }
  .edition__svg {
    width: 100%;
    height: 100%;
    fill: #fff;
    pointer-events: none; }
  @media only screen and (min-width: 360px) {
    .edition {
      width: 30px; }
      .edition--visible {
        width: 135px; }
      .edition__button {
        width: 30px;
        height: 30px; } }

.indicator {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 25px;
  height: 25px;
  overflow: hidden; }
  @media only screen and (min-width: 360px) {
    .indicator {
      width: 30px;
      height: 30px; } }
  .indicator__svg {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;
    width: 100%;
    fill: #dd4b1e;
    opacity: 0;
    visibility: hidden;
    transform: translateX(100%);
    transition: opacity 0.2s, transform 0.2s; }
    .indicator__svg--emo-1 {
      fill: #e24114; }
    .indicator__svg--emo-2 {
      fill: #c75a14; }
    .indicator__svg--emo-3 {
      fill: #ac7314; }
    .indicator__svg--emo-4 {
      fill: #918c14; }
    .indicator__svg--emo-5 {
      fill: #76a514; }
    .indicator__svg--emo-6 {
      fill: #5bbe14; }
    .indicator__svg--emo-7 {
      fill: #40d714; }
    .indicator__svg--emo-8 {
      fill: #25f014; }
    .indicator__svg--visible {
      opacity: 1;
      visibility: visible;
      transform: translateX(0); }
